import Loading from '../../public/Loading'
import LoadingImg from '../../public/loadingImg'
import None from '../../public/noNumber'
export default {
  name: "myAplication",
  data(){
    return{
      api:this.GLOBAL.baseURL,
      value:'',
      options:[
        {label:'全部',value:'0'},
        {label:'待处理',value:'1'},
        {label:'已同意',value:'2'},
        {label:'已谢绝',value:'3'},
      ],
      palyerList:[],
      total:0,
      isLoading:false
    }
  },
  components:{
    None,
    LoadingImg,
    Loading
  },
  methods:{
    toPublish(item,index){
      this.$router.push({
        path: '/publishPage',
        query:{
          information: item.activity_id
        }
      })
    },
    getInvite(val){
      this.isLoading = true
      this.$http.post(this.api + '/ErollListMy',{
        token: localStorage.getItem('token'),
        page: val
      })
        .then(res=>{
          if(res.data.code == 1){
            if(res.data.data.data){
              this.total = res.data.data.total
              this.palyerList = res.data.data.data

            }
            this.isLoading = false
          }else if(res.data.code == 3){
           this.$http.post(this.api + '/home/index/token')
             .then(res=>{
               localStorage.setItem('token',res.data.data)
               this.getInvite(val)
             })
          }else if(res.data.code == 0){
            alert(res.data.msg)
            this.isLoading = false
          }
        })
    },
    handleCurrentChange(val){
      this.getInvite(val)
    },
    backInvit(item,index){
      this.$confirm('此操作将撤回对该活动的志愿者申请, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.returnBM(item,index)

      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //撤回报名
    returnBM(item,index){
      this.$http.post(this.api + '/ErollD',{
        token: localStorage.getItem('token'),
        enroll_id: item.enroll_id
      })
        .then(res=>{
          if(res.data.code == 1){
            item.status = 1
            this.$message({
              type: 'success',
              message: '撤回成功!'
            });
          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.returnBM(item,index)
              })
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
  },
  mounted() {
    this.getInvite(1)
  }
}



